import React, { useState, useContext } from "react";
import { UserContext } from "../../App";
import { useNavigate, Link } from "react-router-dom";
import M from "materialize-css";

const ChangePassword = (props) => {
  const [oldPassword, setoldPassword] = useState("");
  const [newPassword, setPass] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const { user, token, settingUser } = useContext(UserContext);
  const navigate = useNavigate();
  const handleSubmit = (e) => {
    e.preventDefault();
    fetch("/user/modifyPassword", {
      method: "PUT",
      body: JSON.stringify({
        oldPassword,
        newPassword,
        confirmPassword,
      }),
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
    })
      .then((response) => response.json())
      .then((res) => {
        // console.log(res);
        if (res.message) {
          M.toast({
            html: res.message,
            classes: "#43a047 green darken-1",
          });
          navigate("/user/account");
        } else {
          M.toast({ html: res.error, classes: "#c62828 red darken-3" });
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <div className="auth-form-container">
      <h2>Change Password</h2>
      <form className="login-form" onSubmit={handleSubmit}>
        <label style={{ color: "blue" }} htmlFor="email">
          OldPassword
        </label>
        <input
          value={oldPassword}
          onChange={(e) => setoldPassword(e.target.value)}
          type="password"
          placeholder="*********"
          id="email"
          name="email"
        />
        <label style={{ color: "blue" }} htmlFor="password">
          NewPassword
        </label>
        <input
          value={newPassword}
          onChange={(e) => setPass(e.target.value)}
          type="password"
          placeholder="********"
          id="password"
          name="password"
        />
        <label style={{ color: "blue" }} htmlFor="password">
          ConfirmPassword
        </label>
        <input
          value={confirmPassword}
          onChange={(e) => setConfirmPassword(e.target.value)}
          type="password"
          placeholder="********"
          id="passord"
          name="password"
        />
        <button type="submit" className="log-btn">
          Submit
        </button>
      </form>
    </div>
  );
};
export default ChangePassword;
