import React, { useState, useContext, useEffect } from "react";
import Button from "react-bootstrap/Button";
import ListGroup from "react-bootstrap/ListGroup";
import { Link, useNavigate } from "react-router-dom";
import spinner from "../image/spinner.gif";
import { UserContext } from "../App";
import M from "materialize-css";

function SinglePage() {
  const navigate = useNavigate();
  const { user, token } = useContext(UserContext);
  const [post, setPost] = useState();
  const [change, setChange] = useState();
  const [text, setText] = useState("");
  const makeComment = (e) => {
    e.preventDefault();
    if (!text || text === "") {
      M.toast({
        html: "Please write the comment",
        classes: "#c62828 red darken-3",
      });
      return;
    }
    fetch("/comment", {
      method: "put",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({
        postId: post._id,
        text,
      }),
    })
      .then((res) => res.json())
      .then((result) => {
        if (result.error) {
          M.toast({
            html: result.error,
            classes: "#c62828 red darken-3",
          });
          return;
        }
        setPost(result);
        setChange(true);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  function handleClick() {
    fetch("/deleteComment", {
      method: "put",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({
        postId: post._id,
      }),
    })
      .then((res) => res.json())
      .then((result) => {
        if (result.error) {
          M.toast({ html: result.error, classes: "#c62828 red darken-3" });
        } else {
          M.toast({
            html: "deleted the comment successfully",
            classes: "#43a047 green darken-3",
          });
          setPost(result);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }
  useEffect(() => {
    const id = JSON.parse(sessionStorage.getItem("id"));
    fetch(`/post/${id}`, {
      method: "get",
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
        return res.json();
      })
      .then((result) => {
        if (!result) {
          M.toast({
            html: "error while fethcing the post",
            classes: "#c62828 red darken-3",
          });
          return;
        }
        if (result.error) {
          M.toast({
            html: result.error,
            classes: "#c62828 red darken-3",
          });
          return;
        }
        setPost(result.post);
        if (user) {
          const idx = result.post.comments.findIndex((comment) => {
            return comment.postedBy._id === user._id;
          });
          console.log(idx);
          if (idx !== -1) {
            setText(result.post.comments[idx].text);
            setChange(true);
          }
        }
        // console.log(result);
      });
  }, []);
  return (
    <>
      {post ? (
        <div class="post-container">
          <div className="post-card">
            <img className="post-img" alt="post" src={post.photo} />
            <blockquote className="flow-text">{post.title}</blockquote>
            <blockquote>{post.body}</blockquote>
            <div className="comment-section">
              <form onSubmit={makeComment}>
                <input
                  type="text"
                  value={text}
                  onChange={(event) => setText(event.target.value)}
                />
                <Button type="submit" className="btn">
                  {change ? "Edit comment" : "Make Comment"}
                </Button>
              </form>
              {user &&
                (user._id === post.postedBy._id ||
                  user.role === "moderator") && (
                  <Link className="btn" to="/editpost">
                    Edit Post
                  </Link>
                )}
            </div>
          </div>
          <div id="bottom-section" className="post-card">
            <div>
              <span style={{ float: "right" }}>
                {user &&
                  post.comments.some(
                    (comm) => comm.postedBy._id === user._id
                  ) && (
                    <i
                      class="fa fa-trash f-trash btn hint"
                      aria-hidden="true"
                      onClick={handleClick}
                    >
                      <h7 className="hidden-text">Delete Your comment</h7>
                    </i>
                  )}
              </span>
              {/* {console.log(post)} */}
              <ListGroup className="list-group-flush">
                {post.comments.length === 0 ? (
                  <h6 className="flow-text">No Comments</h6>
                ) : (
                  post.comments.map((comment) => {
                    return (
                      <p className="comment">
                        <p className="title">{comment.postedBy.name}</p>
                        <p>
                          {comment.text}
                          {/* {console.log(comment)} */}
                        </p>
                      </p>
                    );
                  })
                )}
              </ListGroup>
            </div>
          </div>
        </div>
      ) : (
        <img
          className="center"
          alt="loader"
          src={spinner}
          style={{ margin: "auto" }}
        />
      )}
    </>
  );
}

export default SinglePage;
