import React, { Fragment, useState, useContext, useEffect } from "react";
import Card from "./Card";
import spinner from "../../image/spinner.gif";
import { UserContext } from "../../App";
import Popup from "../../components/Popup";
import M from "materialize-css";

function Home() {
  const { user, token } = useContext(UserContext);
  const [posts, setPost] = useState([]);
  const [newposts, setnewPost] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");
  const [image, setImage] = useState(undefined);
  const [url, setUrl] = useState(undefined);
  const togglePopup = () => {
    setIsOpen(!isOpen);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (image) {
      uploadPic();
    } else {
      uploadFields();
    }
  };

  const [search, setSearch] = useState("");
  function getPosts() {
    fetch(`/allpost`, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    })
      .then((response) => response.json())
      .then((result) => {
        setPost(result.posts);
        setnewPost(result.posts);
        // console.log(result);
      });
  }
  async function filter(text) {
    if (text === "") {
      setPost(newposts);
      return;
    }
    var updatedList = [...newposts];
    updatedList = updatedList.filter((post) => {
      return post.title.toLowerCase().indexOf(text.toLowerCase()) !== -1;
    });
    setPost(updatedList);
  }
  function handleSubmit1(value) {
    setSearch(value);
    filter(value);
  }
  const uploadFields = () => {
    const pic = url;
    fetch("/createpost", {
      method: "post",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({
        title,
        body,
        pic,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.error) {
          M.toast({ html: data.error, classes: "#c62828 red darken-3" });
        } else {
          getPosts();
          M.toast({
            html: "Created post Successfully",
            classes: "#43a047 green darken-1",
          });
          togglePopup();
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const uploadPic = () => {
    const data = new FormData();
    data.append("file", image);
    data.append("upload_preset", "pinimages");
    data.append("cloud_name", "dwy8fafsq");
    fetch("https://api.cloudinary.com/v1_1/dwy8fafsq/image/upload", {
      method: "post",
      body: data,
    })
      .then((res) => res.json())
      .then((data) => {
        setUrl(data.url);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  useEffect(() => {
    if (url) {
      uploadFields();
    }
  }, [url]);
  useEffect(() => {
    getPosts();
    sessionStorage.removeItem("id");
  }, []);
  const val = ["small", "medium", "large"];
  return (
    <Fragment>
      {posts ? (
        <div className="just-padding">
          <div className="search-container">
            <input
              type="text"
              className="browser-default"
              value={search}
              placeholder="Type here.."
              onChange={(e) => handleSubmit1(e.target.value)}
              name="search"
            />
          </div>
          <div style={styles.container} className="post-filling">
            {posts.map((post, index) => {
              return (
                <Card
                  _id={post._id}
                  size={val[index % 3]}
                  title={post.title}
                  post={post}
                  url={post.photo}
                  key={index}
                  likes={post.likes}
                />
              );
            })}
            <div>
              <button
                className="btn pop-btn"
                value="Click to Open Popup"
                onClick={togglePopup}
              >
                +
              </button>
              {isOpen && (
                <Popup
                  content={
                    <>
                      <b>Create Post</b>
                      <form
                        className="create-post-form"
                        onSubmit={handleSubmit}
                      >
                        <label style={{ color: "blue" }} htmlFor="name">
                          Title
                        </label>
                        <input
                          value={title}
                          name="title"
                          onChange={(e) => setTitle(e.target.value)}
                          id="name"
                          placeholder="Title"
                        />
                        <label style={{ color: "blue" }} htmlFor="email">
                          Body
                        </label>
                        <input
                          value={body}
                          onChange={(e) => setBody(e.target.value)}
                          type="text"
                          id="body"
                          placeholder="A small Description"
                          name="body"
                        />
                        <div className="file-field input-field">
                          <div className="btn #64b5f6 blue darken-1">
                            <span>Choose Photo</span>
                            <input
                              type="file"
                              onChange={(e) => setImage(e.target.files[0])}
                            />
                          </div>
                          <div className="file-path-wrapper">
                            <input className="file-path validate" type="text" />
                          </div>
                        </div>
                        <button type="submit" className="log-btn">
                          Submit
                        </button>
                      </form>
                    </>
                  }
                  handleClose={togglePopup}
                />
              )}
            </div>
          </div>
        </div>
      ) : (
        <img className="center" alt="loader" src={spinner} />
      )}
    </Fragment>
  );
}
const styles = {
  container: {
    margin: "0",
    width: "100vw",
    display: "grid",
    gridTemplateColumns: "repeat(auto-fill, 280px)",
    gridAutoRows: "10px",
    position: "absolute",
    left: "50%",
    marginTop: "5%",
    transform: "translateX(-50%)",
    justifyContent: "center",
  },
};

export default Home;
