import React, { useState,useEffect} from "react";
import { useNavigate } from "react-router-dom";
import M from "materialize-css";

const Register = (props) => {
  const [email, setEmail] = useState("");
  const [password, setPass] = useState("");
  const [name, setName] = useState("");
  const [image, setImage] = useState("");
  const [url, setUrl] = useState(undefined);
  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();
     if (image) {
       uploadPic();
     } else {
       uploadFields();
     }
  };
  const uploadPic = () => {
    const data = new FormData();
    data.append("file", image);
    data.append("upload_preset", "pinimages");
    data.append("cloud_name", "dwy8fafsq");
    fetch("https://api.cloudinary.com/v1_1/dwy8fafsq/image/upload", {
      method: "post",
      body: data,
    })
      .then((res) => res.json())
      .then((data) => {
        setUrl(data.url);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const uploadFields = () => {
    if (
      !/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
        email
      )
    ) {
      M.toast({ html: "invalid email", classes: "#c62828 red darken-3" });
      return;
    }
    fetch("/signup", {
      method: "post",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name,
        password,
        email,
        pic: url,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data);
        if (data.error) {
          M.toast({ html: data.error, classes: "#c62828 red darken-3" });
        } else {
          M.toast({ html: data.message, classes: "#43a047 green darken-1" });
          navigate('/login');
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
   useEffect(() => {
     if (url) {
       uploadFields();
     }
   }, [url]);
  return (
    <div className="auth-form-container">
      <h2>Register</h2>
      <form className="register-form" onSubmit={handleSubmit}>
        <label style={{ color: "blue" }} htmlFor="name">
          Full name
        </label>
        <input
          value={name}
          name="name"
          onChange={(e) => setName(e.target.value)}
          id="name"
          placeholder="full Name"
        />
        <label style={{ color: "blue" }} htmlFor="email">
          email
        </label>
        <input
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          type="email"
          placeholder="youremail@gmail.com"
          id="email"
          name="email"
        />
        <label style={{ color: "blue" }} htmlFor="password">
          password
        </label>
        <input
          value={password}
          onChange={(e) => setPass(e.target.value)}
          type="password"
          placeholder="********"
          id="password"
          name="password"
        />
        <div className="file-field input-field">
          <div className="btn #64b5f6 blue darken-1">
            <span>Choose DP</span>
            <input type="file" onChange={(e) => setImage(e.target.files[0])} />
          </div>
          <div className="file-path-wrapper">
            <input className="file-path validate" type="text" />
          </div>
        </div>
        <button type="submit" className="log-btn">
          Register
        </button>
      </form>
    </div>
  );
};

export default Register;
