import React, { useEffect, useState, useContext, Fragment } from "react";
import { UserContext } from "../../App";
import spinner from "../../image/spinner.gif";
import { useNavigate, Link } from "react-router-dom";
import M from "materialize-css";

const Posts = () => {
  const navigate = useNavigate();
  const [userProfile, setProfile] = useState(null);

  const { user, token } = useContext(UserContext);
  function handleImageClick(item) {
    sessionStorage.setItem("id", JSON.stringify(item._id));
    navigate("/post");
  }
  useEffect(() => {
    if (!user) {
      navigate(-1);
    }
    fetch(`/user/${user._id}`, {
      headers: {
        Authorization: "Bearer " + token,
      },
    })
      .then((res) => res.json())
      .then((result) => {
        // console.log(result)
        setProfile(result);
      });
  }, []);
  function handleClick(item) {
    fetch(`/deletepost/${item._id}`, {
      method: "delete",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({}),
    })
      .then((res) => res.json())
      .then((result) => {
        if (result.error) {
          M.toast({ html: result.error, classes: "#c62828 red darken-3" });
        } else {
          M.toast({
            html: "deleted the post successfully",
            classes: "#43a047 green darken-3",
          });
          navigate(0);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }
  return (
    <Fragment>
      {userProfile ? (
        <div className="post-div">
          <div
            style={{ maxWidth: "550px", margin: "0px auto", marginTop: "10%" }}
          >
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                flexWrap: "wrap",
                marginBottom: "18px",
                borderBottom: "1px solid grey",
              }}
            >
              <div className="post-img">
                <img
                  style={{
                    width: "100%",
                    height: "100%",
                    borderRadius: "80px",
                  }}
                  src={userProfile.user.pic}
                  alt="pic"
                />
              </div>
              <div>
                <h5 style={{ fontWeight: "500" }}>{userProfile.user.email}</h5>
                <div
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <h6>{userProfile.posts.length} posts</h6>
                </div>
                <h6 style={{ fontWeight: "500" }}>{userProfile.user.name}</h6>
              </div>
            </div>

            <div id="bottom-section" className="gallery">
              {userProfile.posts.map((item) => {
                return (
                  <div className="pic">
                    <img
                      key={item._id}
                      className="item"
                      src={item.photo}
                      alt={item.title}
                      onClick={() => handleImageClick(item)}
                    />
                    <i
                      class="fa fa-trash f-trash btn hint"
                      aria-hidden="true"
                      onClick={() => handleClick(item)}
                    >
                      <p className="hidden-text">Delete the post</p>
                    </i>
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      ) : (
        <img
          className="center"
          alt="loader"
          src={spinner}
          style={{ margin: "auto" }}
        />
      )}
    </Fragment>
  );
};

export default Posts;
