import React, { Fragment, useEffect, useState, useContext } from "react";
import "./UserPost.css";
import { Link, useNavigate } from "react-router-dom";
import M from "materialize-css";
import { UserContext } from "../../App";

function Post() {
  const navigate = useNavigate();
  const { token } = useContext(UserContext);
  const [posts, setPosts] = useState([]);
  function handleDelete(id) {
    fetch(`/moderator/deletepost/${id}`, {
      method: "delete",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({}),
    })
      .then((res) => res.json())
      .then((result) => {
        if (result.error) {
          M.toast({ html: result.error, classes: "#c62828 red darken-3" });
        } else {
          M.toast({
            html: "deleted the post successfully",
            classes: "#43a047 green darken-3",
          });
          navigate(0);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }
  useEffect(() => {
    fetch("/allpost", {
      method: "GET",
      headers: {
        Authorization: "Bearer " + token,
      },
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.error) {
          M.toast({
            html: "error while loading the posts",
            classes: "#c62828 red darken-3",
          });
        } else {
          setPosts(data.posts);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  return (
    <Fragment>
      {posts && (
        <div class="grid-container">
          <div class="grid-item grid-change">
            <span className="heading">NAME</span>{" "}
            <span className="heading">PostedBy</span>{" "}
            <span className="heading">Delete</span>{" "}
          </div>
          {posts.map((post) => {
            return (
              <div class="grid-item grid-change">
                <span>
                  <Link to="/post" state={{ post: post }}>
                    {post.title}
                  </Link>
                </span>
                <span>{post.postedBy.email}</span>
                <span>
                  <i
                    className="fa fa-trash btn"
                    onClick={() => handleDelete(post._id)}
                  ></i>
                </span>
              </div>
            );
          })}
        </div>
      )}
    </Fragment>
  );
}

export default Post;
