import React, { Fragment, useState, useContext, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { UserContext } from "../../App";
import { PostContext } from "../../App";
import M from "materialize-css";

function EditPost() {
  const p = sessionStorage.getItem("post");
  const navigate = useNavigate();
  // console.log(location.state.post);
  // console.log(p);
  const [post, setPost] = useState(JSON.parse(p));
  const { token } = useContext(UserContext);
  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");
  const [image, setImage] = useState(undefined);
  const [url, setUrl] = useState(undefined);
  const handleSubmit = (e) => {
    e.preventDefault();
    if (image) {
      uploadPic();
    } else {
      uploadFields();
    }
  };
  const uploadFields = () => {
    const pic = url;
    const postId = post._id;
    // console.log(title,body,pic,postId);
    fetch("/editpost", {
      method: "put",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({
        title,
        body,
        pic,
        postId,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.error) {
          M.toast({ html: data.error, classes: "#c62828 red darken-3" });
        } else {
          M.toast({
            html: "Edited post Successfully",
            classes: "#43a047 green darken-1",
          });
          navigate(-1);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const uploadPic = () => {
    const data = new FormData();
    data.append("file", image);
    data.append("upload_preset", "pinimages");
    data.append("cloud_name", "dwy8fafsq");
    fetch("https://api.cloudinary.com/v1_1/dwy8fafsq/image/upload", {
      method: "post",
      body: data,
    })
      .then((res) => res.json())
      .then((data) => {
        setUrl(data.url);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  useEffect(() => {
    if (url) {
      uploadFields();
    }
  }, [url]);
  useEffect(() => {
    const id = JSON.parse(sessionStorage.getItem("id"));
    fetch(`/post/${id}`, {
      method: "get",
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
        return res.json();
      })
      .then((result) => {
        if (!result) {
          M.toast({
            html: "error while fethcing the post",
            classes: "#c62828 red darken-3",
          });
          return;
        }
        if (result.error) {
          M.toast({
            html: result.error,
            classes: "#c62828 red darken-3",
          });
          return;
        }
        setPost(result.post);
        setTitle(result.post.title);
        setBody(result.post.body);
      });
  }, []);
  return (
    <Fragment>
      {post && (
        <div className="edit-div">
          <h1>Edit Post</h1>
          <form className="create-post-form" onSubmit={handleSubmit}>
            <label style={{ color: "blue" }} htmlFor="name">
              Title
            </label>
            <input
              value={title}
              name="title"
              onChange={(e) => setTitle(e.target.value)}
              id="name"
              placeholder="Title"
            />
            <label style={{ color: "blue" }} htmlFor="email">
              Body
            </label>
            <input
              value={body}
              onChange={(e) => setBody(e.target.value)}
              type="text"
              id="body"
              placeholder="A small Description"
              name="body"
            />
            <div className="file-field input-field">
              <div className="btn #64b5f6 blue darken-1">
                <span>Choose Photo</span>
                <input
                  type="file"
                  onChange={(e) => setImage(e.target.files[0])}
                />
              </div>
              <div className="file-path-wrapper">
                <input className="file-path validate" type="text" />
              </div>
            </div>
            <button type="submit" className="log-btn">
              Submit
            </button>
          </form>
        </div>
      )}
    </Fragment>
  );
}

export default EditPost;
