import React, { useEffect, useState,useContext, Fragment} from 'react'
import { UserContext } from "../../App";
import M from "materialize-css";
import { useParams,useNavigate} from 'react-router-dom';

function EditStatus() {
    const navigate = useNavigate();
    const { token } = useContext(UserContext);
    const [user, setUser] = useState([]);
    const [status, setStatus] = useState("");
    const { id } = useParams();

    function handleSubmit(e) {
        e.preventDefault();
        // console.log(user._id)
        const id = user._id;
        fetch("/moderator/user/status", {
          method: "PUT",
          body: JSON.stringify({
           id
          }),
            headers: {
              "Authorization" : "Bearer " + token,
            "Content-Type": "application/json",
          },
        })
          .then((response) => response.json())
          .then((data) => {
            if (data.error) {
              M.toast({
                html: data.error,
                classes: "#c62828 red darken-3",
              });
            } else {
              M.toast({
                html: data.message,
                classes: "#43a047 green darken-1",
              });
              navigate("/moderator/users");
            }
          })
          .catch((err) => {
            console.log(err);
          });
    }
    useEffect(() => {
      fetch(`/moderator/user/${id}`, {
        method: "GET",
        headers: {
          Authorization: "Bearer " + token,
        },
      })
        .then((response) => response.json())
        .then((data) => {
          if (data.error) {
            M.toast({
              html: "error while loading the users",
              classes: "#c62828 red darken-3",
            });
          } else {
            setUser(data.user[0]);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    }, [user]);
    return (
      <Fragment>
        {user && (
          <div className="auth-form-container">
            <h2>Edit Status</h2>
            <form className="login-form" onSubmit={handleSubmit}>
              <label style={{ color: "blue" }} htmlFor="text">
                Status
              </label>
              <div class="input-field col s12">
                <select
                  onChange={(e) => setStatus(e.target.value)}
                  required
                  value={status}
                  className="browser-default"
                >
                  <option value="" disabled selected>
                    Choose Category
                  </option>
                  {user.condition === "Unblock" && (
                    <option value="1">block</option>
                  )}
                  {user.condition === "block" && (
                    <option value="1">Unblock</option>
                  )}
                </select>
              </div>
              <button type="submit" className="log-btn">
                Edit
              </button>
            </form>
          </div>
        )}
      </Fragment>
    );
}

export default EditStatus