import "./App.css";
import React, { useEffect, useState } from "react";
import Login from "./components/User/Login";
import Register from "./components/User/Register";
import Navbar from "./components/Home/Navbar";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Home from "./components/Home/Home";
import SinglePage from "./components/SinglePage";
import Profile from "./components/User/Profile";
import ForgotPassword from "./components/User/ForgotPassword";
import EditPassword from "./components/User/EditPassword";
import ChangePassword from "./components/User/ChangePassword";
import EditProfile from "./components/User/EditProfile";
import Posts from "./components/User/Posts";
import EditPost from "./components/Post/EditPost";
import Error from "./components/Error";
import Dashboard from "./components/Moderator/Dashboard";
import User from "./components/Moderator/User";
import EditStatus from "./components/Moderator/EditStatus";
import Post from "./components/Moderator/Post";

export const UserContext = React.createContext([]);
export const PostContext = React.createContext([]);
function App() {
  // console.log(localStorage.getItem('user'));
  const [token, setToken] = useState(
    localStorage.getItem("token") !== "undefined"
      ? JSON.parse(localStorage.getItem("token"))
      : ""
  );
  const [user, setUser] = useState(
    localStorage.getItem("user") !== "undefined"
      ? JSON.parse(localStorage.getItem("user"))
      : {}
  );
  function settingUser(user, token) {
    setUser(user);
    setToken(token);
    localStorage.setItem("user", JSON.stringify(user));
    localStorage.setItem("token", JSON.stringify(token));
  }
  useEffect(() => {
    localStorage.setItem("user", JSON.stringify(user));
  }, []);
  return (
    <>
      <PostContext.Provider>
        <UserContext.Provider value={{ user, token, settingUser }}>
          <Router>
            <Navbar />
            <Routes>
              <Route index exact path="/" element={[<Home />]} />
              <Route path="/login" element={<Login />}></Route>
              <Route path="/register" element={<Register />}></Route>
              <Route path="/post" element={<SinglePage />}></Route>
              {user && (
                <Route path="/user/account" element={<Profile />}></Route>
              )}
              <Route
                path="/resetpassword"
                element={<ForgotPassword></ForgotPassword>}
              />
              {user && (
                <Route
                  exact
                  path="/user/updateProfile"
                  element={<EditProfile />}
                />
              )}
              {user && user.role === "moderator" && (
                <Route
                  path="/moderator/user/edit/:id"
                  element={<EditStatus />}
                />
              )}
              {user && user.role === "moderator" && (
                <Route path="/moderator/posts" element={<Post />} />
              )}
              {user && user.role === "moderator" && (
                <Route path="/moderator/users" element={<User />} />
              )}
              {user && user.role === "moderator" && (
                <Route path="/moderator/dashboard" element={<Dashboard />} />
              )}
              {user && <Route path="/editpost" element={<EditPost />} />}
              {user && <Route path="/user/post" element={<Posts />}></Route>}
              <Route path="/reset/:token" element={<EditPassword />} />
              {user && (
                <Route
                  path="/changePassword"
                  element={<ChangePassword />}
                ></Route>
              )}
              <Route path="*" element={<Error />}></Route>
            </Routes>
          </Router>
        </UserContext.Provider>
      </PostContext.Provider>
    </>
  );
}

export default App;
