import React, { Fragment, useEffect, useState, useContext} from "react";
import "./UserPost.css";
import { Link } from "react-router-dom";
import M from 'materialize-css';
import { UserContext } from '../../App';

function User() {
    const { token } = useContext(UserContext);
  const [users, setUsers] = useState([]);
    useEffect(() => {
        fetch("/moderator/users", {
          method: "GET",
          headers: {
            Authorization: "Bearer " + token,
          },
        })
          .then((response) => response.json())
            .then((data) => {
                // console.log(data);
            if (data.error) {
              M.toast({
                html: "error while loading the users",
                classes: "#c62828 red darken-3",
              });
            } else {
              setUsers(data.users);
            }
          })
          .catch((err) => {
            console.log(err);
          });
    }, [])
    
  return (
    <Fragment>
      {users && (
        <div class="grid-container">
          <div class="grid-item">
            <span className="heading">NAME</span>{" "}
            <span className="heading">EMAIL</span>{" "}
            <span className="heading">Role</span>{" "}
            <span className="heading">Condition</span>
          </div>
          {users.map((user) => {
            return (
              <div class="grid-item">
                <span
                >
                  {user.name}
                </span>
                
                <span>{user.email}</span> <span>{user.role}</span>
                <span>
                  <Link to={`/moderator/user/edit/${user._id}`}>
                    {user.condition}
                  </Link>
                </span>
              </div>
            );
          })}
        </div>
      )}
    </Fragment>
  );
}

export default User
