import React, { Fragment, useContext, useEffect, useState } from "react";
import { Link,useNavigate} from "react-router-dom";
import { UserContext } from "../../App";
import M from "materialize-css";

const us = JSON.parse(sessionStorage.getItem("user"));
function Profile() {
     const navigate = useNavigate();
     const { settingUser, user } = useContext(UserContext);
     const [freshuser, setFreshUser] = useState(user ? user : us);
     function singnmeout() {
       fetch("/signout", {
         method: "GET",
         headers: {
           Accept: "application/json",
           "Content-Type": "application/json",
         },
         credentials: "include",
       })
         .then((res) => {
           // console.log(isAuth);
           M.toast({
             html: "successfully logged out",
             classes: "#43a047 green darken-1",
           });
           settingUser(null, "");
           navigate("/login");
         })
         .catch((err) => {
           console.log(err);
         });
     }
    useEffect(() => {
      sessionStorage.setItem("user", JSON.stringify(freshuser));
    }, [freshuser]);
  return (
    <Fragment>
      <div className ="profile-container">
      <h1>{freshuser.name}'s Profile</h1>
      <div className="profileContainer">
        <div>
          <h1>My Profile</h1>
          <img src={freshuser.pic} alt="no profile"></img>
          <Link to="/user/updateProfile">Edit Profile</Link>
          <Link style={{ paddingTop: 0, marginTop: 0 }} to="/user/post">
            My Posts
          </Link>
          {user && user.role === "moderator" && <Link to="/moderator/dashboard"
            style={{ paddingTop: 0, marginTop: 0 }}>
            Dashboard
          </Link>}
        </div>
        <div>
          <div>
            <h4>Full Name</h4>
            <p>{freshuser.name}</p>
          </div>
          <div>
            <h4>Email</h4>
            <p>{freshuser.email}</p>
          </div>
          <div>
            <Link to="/changePassword">Change Password</Link>
            <Link to="/login" onClick={singnmeout}>
              SignOut
            </Link>
          </div>
        </div>
      </div>
      </div>
    </Fragment>
  );
}

export default Profile;
