import React,{useContext, useEffect, useState} from "react";
import { UserContext } from '../../App';
import { saveAs } from "file-saver";
import M from 'materialize-css';
import {useNavigate} from "react-router-dom";

function Card(props) {
  const navigate = useNavigate();
  const { user, token } = useContext(UserContext);
  console.log(props.likes);
  const [like, setLike] = useState(user ? (props.likes.includes(user._id) ? true : false) : false);
  const handleClick = () => {
    let url = props.url;
    saveAs(url, props.title);
  };
  const handlepost = () => {
    sessionStorage.setItem('id', JSON.stringify(props.post._id));
    navigate('/post');
  }
  const likePost = (id) => {
    if (like) {
      fetch("/unlike", {
        method: "put",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify({
          postId: props._id,
        }),
      })
        .then((res) => res.json())
        .then((result) => {
          console.log(result);
          if (result.error) {
            M.toast({
              html: result.error,
              classes: "#c62828 red darken-3",
            });
            return;
          }
          setLike((prev) => !prev);
        })
        .catch((err) => {
          console.log(err);
        });
    }
    else {
      fetch("/like", {
        method: "put",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify({
          postId: props._id,
        }),
      })
        .then((res) => res.json())
        .then((result) => {
          if (result.error) {
            M.toast({
              html: result.error,
              classes: "#c62828 red darken-3",
            });
            return;
          }
          setLike((prev) => !prev);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };
  return (
    <div
      className="card-div"
      style={{
        ...styles.card,
        ...styles[props.size],
      }}
    >
      <img
          src={props.url}
        alt="img"
        onClick={handlepost}
          className="card-img"
          style={{
            minWidth: "100%",
            maxWidth: "100%",
            display: "block",
            minHeight: "100%",
            maxHeight: "100%",
            borderRadius: "16px",
            cursor : "pointer",
          }}
        />
      <button className="card-btn1" onClick={likePost}>
        <i
          style={{
            color: like ? "red" : "",
          }}
          class="fa fa-thumbs-up"
          aria-hidden="true"
        ></i>
      </button>
      <button className="card-btn2" onClick={handleClick}>
        <i class="fa fa-download" aria-hidden="true"></i>
      </button>
    </div>
  );
}
const styles = {
  card: {
    margin: "15px 10px",
    padding: 0,
    position : "relative"
  },
  small: {
    gridRowEnd: "span 26",
  },
  medium: {
    gridRowEnd: "span 33"
  },
  large: {
    gridRowEnd: "span 45",
  },
};

export default Card;
