import React, { useState, useContext } from "react";
import { UserContext } from "../../App";
import { useNavigate,Link} from "react-router-dom";
import M from "materialize-css";

const Login = (props) => {
  const [email, setEmail] = useState("");
  const [password, setPass] = useState("");
  const { user, settingUser } = useContext(UserContext);
  const navigate = useNavigate();
  const handleSubmit = (e) => {
    e.preventDefault();
    fetch("/signin", {
      method: "POST",
      body: JSON.stringify({
        email,
        password,
      }),
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((response) => response.json())
      .then((res) => {
        // console.log(res);
        if (res.user) {
          M.toast({html:"logged in successfully",classes:"#43a047 green darken-1"})
          settingUser(res.user, res.token);
          navigate('/');
        }
        else {
          M.toast({ html: res.error, classes: "#c62828 red darken-3" });
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <div className="auth-form-container">
      <h2>Login</h2>
      <form className="login-form" onSubmit={handleSubmit}>
        <label style={{ color: "blue" }} htmlFor="email">
          email
        </label>
        <input
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          type="email"
          placeholder="youremail@gmail.com"
          id="email"
          name="email"
        />
        <label style={{ color: "blue" }} htmlFor="password">
          password
        </label>
        <input
          value={password}
          onChange={(e) => setPass(e.target.value)}
          type="password"
          placeholder="********"
          id="password"
          name="password"
        />
        <button type="submit" className="log-btn">
          Log In
        </button>
        <Link to="/resetpassword" style ={{color : "green"}}>forgot Password</Link>
      </form>
    </div>
  );
};
export default Login;
